import React, { Component } from 'react'
import Note from './../components/Notes/Note';

export default class notes extends Component {
  render() {
    return (
        <div className="row">
            <div className="col-12 grid-margin">
                <div className="card">
                    
                    <div className="card-body" id="chat-body" style={{ background : '#f2f8f9',padding: '15px', overflowY : 'scroll'}}>
                    <Note date = "12 July 2018" value="Hello this is my first note, and I am just testing this note app."/>
                    <Note date = "12 July 2018" value="Hello this is my first note, and I am just testing this note app."/>
                    </div>
                    
                    <div className="card-body" id="chat-controls" style={{ padding : '5px', position : 'fixed', bottom : '0', background : '#f3f8f7' }}>
                        <form>
                            <InputGroup buttonText="SAVE" placeholder="Type Note..."/> 
                        </form>
                    </div>
                    
                    
                </div>
            </div>
        </div>
    )
  }
}
